@import url(https://fonts.googleapis.com/css2?family=Poller+One&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.font-poller {
  font-family: Poller One , cursive;
}

.bg-dark {
  background-color: rgba(31, 29, 29, 0.733);
}

.bg-galaxy {
  background: rgb(19, 17, 17);
}

.bg-gradient__galaxy {
  background: linear-gradient(90deg, rgba(19, 17, 17, 1) 0%, rgba(68, 77, 75, 1) 49%, rgba(223, 209, 209, 1) 85%);
}

.text-center {
  text-align: center;
}

.text-color__light {
  color: rgb(241, 234, 234);
}

.m-1 {
  margin: calc(1% * 0.25);
}

.m-2 {
  margin: calc((0.5vw + 0.5vh) * 0.25);
}

.d-flex {
  display: flex;
}

.justify-content__center {
  justify-content: center;
}

.align-items__center {
  align-items: center;
}

.input-text {
  border: none;
  border-radius: 2px;
  margin: calc((0.3vw + 0.3vh) * 1.2);
  max-height: 35px;
  max-width: 250px;
  min-height: 32px;
  width: 200px;
}

.container {
  box-sizing: border-box;
  height: auto;
  max-width: 100vw;
  padding: calc(1% * 0.25);
  width: 100%;
}

.bg-image-galaxy {
  background-image: url(/starwars-datatable-filters/static/media/galaxy.5b3d8ebf.jpg);
  background-repeat: repeat;
  min-height: 100vh;
  width: 100%;
}

